<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6">
        <div class="tree">
          <Tree :data="unitList" @check="checkUnit" :selectAll="true"></Tree>
        </div>
        <div class="user-table">
          <Table 
            :header="headerData" 
            :data="userList" 
            :showPage="false"
            @check="checkUser"
          ></Table>
        </div>
        <div class="operate">
          <el-row :gutter="20">
            <el-col :span="12"><el-input v-model="serachData.name" placeholder="请输入姓名" @input="changeName"></el-input></el-col>
            <el-col :span="12"><el-button @click="dialogData.show=true">高级查询</el-button></el-col>
            <el-col :span="12"><el-button @click="noUser = false;addUser()">添加人员</el-button></el-col>
            <el-col :span="12">
              <a :href="downloadimportdemo"><el-button>下载导入模板</el-button></a>
            </el-col>
            <el-col :span="12">
              <el-upload
                class="avatar-uploader"
                :action="importuser"
                :with-credentials="true"
                :show-file-list="false"
                :on-success="uploadCb">
                <el-button>导入</el-button>
              </el-upload>
            </el-col>
            <el-col :span="12">
              <el-upload
                class="avatar-uploader"
                :action="importimage"
                :with-credentials="true"
                :show-file-list="false"
                :on-success="uploadCb">
                <el-button>导入相片</el-button>
              </el-upload>
            </el-col>
          </el-row>
        </div>
      </el-col>
      <el-col :span="18">
        <div class="user-detail">
          <User v-if="!noUser" :data="userData"></User>
          <div v-else class="no-user">暂无选择人员</div>
        </div>
        <div class="user-operate">
          <template v-if="userData.identityCard">
            <el-button type="danger" @click="delUser">删除</el-button>
            <a :href="exportuser + userData.code" class="button-a"><el-button>导出个人信息</el-button></a>
            <a :href="exportincomecertificate + userData.code" class="button-a"><el-button>导出收入证明</el-button></a>
            <a :href="exportonthejobcertificate + userData.code" class="button-a"><el-button>导出在职证明</el-button></a>
          </template>
          <el-button type="primary" @click="saveUser">保存</el-button>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <SuperSearch :data="dialogData" @success="superPagelist"></SuperSearch>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import Tree from '../../components/tree.vue'
import Table from '../../components/table.vue'
import User from '../../components/user.vue'
// import Dialog from '../../components/dialog/index.vue'
import SuperSearch from '../../components/dialog/superSearch.vue'
import userConfig from '../../config/user.json'
import Config from '../../config/common.json'

export default {
  name: 'Unit',
  components: {
    Tree,
    Table,
    User,
    SuperSearch
    // Dialog
  },
  computed: {
    ...mapState({
      unitList: 'unit',
    }),
  },
  data() {
    return {
      headerData: [
        { key: 'name', label: '姓名' },
        { key: 'companyName', label: '单位' },
      ],
      // 查询条件
      serachData: {
        name: '',
        companyCodeList: []
      },
      noUser: true,
      userList: [],
      userData: {},
      downloadimportdemo: Config.server + '/user/downloadimportdemo',
      exportuser: Config.server + '/user/exportuser?userCode=',
      exportincomecertificate: Config.server + '/user/exportincomecertificate?userCode=',
      exportonthejobcertificate: Config.server + '/user/exportonthejobcertificate?userCode=',
      importuser: Config.server + '/user/importuser',
      importimage: Config.server + '/user/importimage',
      dialogData: {
        title: '高级查询',
        show: false
      }
    }
  },
  methods: {
    ...mapActions([
      'unit', 'nation'
    ]),
    // 单位变化时
    checkUnit(list) {
      this.serachData.companyCodeList = list;
      this.getUserList()
    },
    // 姓名变化时
    changeName() {
      this.getUserList()
    },
    // 获取人员列表
    getUserList() {
      this.request.post('/user/pagelist', this.serachData).then(res => {
        console.log('/user/pagelist', res)
        if (res.status == 200 & res.data.success) {
          this.userList = res.data.data
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      })
    },
    // 选中姓名变化时
    checkUser(opt) {
      this.noUser = false;
      this.getUserData(opt.row.code)
    },
    // 将组织转成数组
    unit2Array(code) {
      let arr = []
      this.unitList.forEach(element => {
        if (element.code == code) {
          arr.push(code)
        } else {
          element.children.forEach(item => {
            if (item.code == code) {
              arr.push(element.code)
              arr.push(item.code)
            }
          })
        }
      });
      return arr
    },
    // 获取人员详情
    getUserData(code) {
      this.request.post('/user/detail', {
        code: code
      }).then(res => {
        if (res.status == 200 & res.data.success) {
          for(let item in this.userData) {
            if (userConfig[item] && userConfig[item].type == 'enums') {
              this.userData[item] = this.unit2Array(res.data.data[item])
            } else if (userConfig[item] && userConfig[item].type == 'multiple') {
              this.userData[item] = res.data.data[item] ? res.data.data[item].split(',') : []
              this.userData[item].forEach((inner, innerIndex) => {
                this.userData[item][innerIndex] = +inner
              })
            } else {
              this.userData[item] = res.data.data[item]
            }
          }
          this.userData.code = res.data.data.code
          this.userData.headPicUrl = Config.server + res.data.data.headPicUrl
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      })
    },
    // 添加人员
    addUser() {
      let data = {}
      for (let item in userConfig) {
        if (userConfig[item].type == 'enums' || userConfig[item].type == 'table') {
          data[item] = []
        } else {
          data[item] = ''
        }
      }
      this.userData = data
    },
    // 保存人员
    saveUser() {
      let reqData = JSON.parse(JSON.stringify(this.userData))
      let url = reqData.code ? '/user/update' : '/user/add'
      reqData.workUnitCode = reqData.workUnitCode[reqData.workUnitCode.length-1]
      reqData.organizationUnitCode = reqData.organizationUnitCode[reqData.organizationUnitCode.length-1]
      reqData.quasiDrivingType = reqData.quasiDrivingType.join(',')
      reqData.specialPeople = reqData.specialPeople.join(',')
      this.request.post(url, reqData).then(res => {
        if (res.status == 200 & res.data.success) {
          this.$message({
            showClose: true,
            message: reqData.code ? '更新人员成功' : '添加人员成功',
            type: 'success'
          });
          this.getUserList()
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      })
    },
    // 导入成功回调
    uploadCb(res) {
      console.log(res)
      if (res.success) {
        this.$message({
          showClose: true,
          message: '导入成功',
          type: 'success'
        });
        this.getUserList()
      } else {
        this.$message({
          showClose: true,
          message: res.message,
          type: 'error'
        });
      }
    },
    // 高级查询
    superPagelist(resData) {
      let reqData = JSON.parse(JSON.stringify(resData))
      for(let item in this.userData) {
        if (userConfig[item] && userConfig[item].type == 'date') {
          reqData[item + 'Begin'] = reqData[item][0]
          reqData[item + 'End'] = reqData[item][1]
          delete reqData[item]
        } else if (userConfig[item].type == 'enum' || userConfig[item].type == 'enums' || userConfig[item].type == 'multiple') {
          // reqData[item] = reqData[item].join(',')
          console.log(reqData[item])
        } else {
          // this.userData[item] = res.data.data[item]
        }
      }
      console.log(reqData)
      this.request.post('/user/superpagelist', reqData).then(res => {
        if (res.status == 200 & res.data.success) {
          this.$message({
            showClose: true,
            message: '查询人员成功',
            type: 'success'
          });
          this.userList = res.data.data || []
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          });
        }
      })
    },
    // 删除人员
    delUser() {
      this.$confirm('确认删除 ' + this.userData.name + ' ?').then(res => {
        if (res == 'confirm') {
          this.request.post('/user/delete', {
          userCode: this.userData.code
        }).then(res => {
          if (res.status == 200 & res.data.success) {
            this.$message({
              showClose: true,
              message: '删除人员成功',
              type: 'success'
            });
            this.getUserList()
            this.noUser = true
          } else {
            this.$message({
              showClose: true,
              message: res.data.message,
              type: 'error'
            });
          }
        })
        }
      }).catch(err => {
        console.log(err)
      });
    }
  },
  mounted() {
    this.unit()
    this.getUserList()
    this.addUser()
  }
}
</script>

<style scoped>
.tree {
  height: 295px;
  overflow: scroll;
  border: 2px #EBEEF5 solid;
  padding-bottom: 20px; 
}
.user-table {
  height: 300px;
  overflow: scroll;
  padding: 10px;
  border: 2px #EBEEF5 solid;
}
.operate {
  padding: 20px 10px 10px;
  border: 2px #EBEEF5 solid;
}
.operate .el-button {
  width: 100%;
  margin-bottom: 10px;
}
.user-detail {
  height: 800px;
  overflow: scroll;
  padding: 10px;
  border: 2px #EBEEF5 solid;
}
.user-operate {
  margin-top: 20px;
  text-align: right;
}
.no-user {
  display:flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.el-button+.button-a, .button-a+.el-button, .button-a+.button-a {
  margin-left: 10px;
}
</style>
